import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
let {
	log
} = console;
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote
} = app;
export default {
	data() {
		return {
			token: uni.getStorageSync('cdj_token'),
			kind: 0,
			arrowDirect: false,
			ware: [],
			recommend: [],
			recommendLength: '',
			value: 1,
			id: '',
			hours: '0',
			minu: 0,
			second: 0,
			attrspec: '',
			spec: '',
			nav: false,
			imgRemote: imgRemote
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			})
		},
		collecting() {
			let statu = this.ware.collect_status,
				status;
			if (statu == 2) {
				status = 1;
			} else {
				status = 2;
			}
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				item_id: this.id,
				appid: appid,
				timeStamp: timeStamp,
				status: status
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			rs.getRequests('changeCollect', params, res => {
				let data = res.data;
				if (data.code == 200) {
					this.ware.collect_status = status;
					statu = status;
					if (statu == 1) {
						rs.Toast('取消收藏');
					} else {
						rs.Toast('收藏成功');
					}
				} else {
					rs.Toast(res.msg);
				}
			});
		},
		showKey() {
			this.$refs.popup.open();
		},
		selectSpec(index) {
			this.kind = index;
			this.attrspec = this.ware.attr[index];
		},
		getItem() {
			let {
				id
			} = this;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				id: id,
				appid: appid,
				timeStamp: timeStamp
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			//单个商品详情
			rs.getRequests('getItemById', params, res => {
				let data = res.data;
				if (data.code == 200) {
					this.info = data.data.info;
					this.ware = data.data;
					if (data.data.attr.length > 0) {
						this.spec = true;
						this.attrspec = data.data.attr[0];
					} else {
						this.spec = false;
					}

					this.hours = data.data.panicActivity.timeRemain / 3600;
					// 推荐列表
					rs.getRequests('itemRecommend', params, res => {
						let data = res.data;
						if (data.code == 200) {
							this.recommend = data.data;
							if (data.data.list.length) {
								this.recommendLength = true;
							}
						}
					});
				}else{
					rs.Toast(data.msg)
				}
			});

		},
		detailPage(id) {
			uni.navigateTo({
				url: 'shopdetail?id=' + id
			});
		},
		minus() {
			if (this.spec) {
				if (this.attrspec.is_float == 1 && !Number.isInteger(Number(this.value))) {
					rs.Toast('购买数量不能为小数');
					return;
				}
			} else {
				if (this.ware.is_float == 1 && !Number.isInteger(Number(this.value))) {
					rs.Toast('购买数量不能为小数');
					return;
				}
			}
			this.value--;
			if (this.value <= 1) {
				this.value = 1;
			}
		},
		plus() {
			if (this.spec) {
				if (this.attrspec.is_float == 1 && !Number.isInteger(Number(this.value))) {
					rs.Toast('购买数量不能为小数');
					return;
				}
			} else {
				if (this.ware.is_float == 1 && !Number.isInteger(Number(this.value))) {
					rs.Toast('购买数量不能为小数');
					return;
				}
			}
			this.value++;
		},
		addCart(cartUrl) {
			if (this.spec) {
				if (this.attrspec.is_float == 1 && !Number.isInteger(Number(this.value))) {
					rs.Toast('购买数量不能为小数');
					return;
				}
			} else {
				if (this.ware.is_float == 1 && !Number.isInteger(Number(this.value))) {
					rs.Toast('购买数量不能为小数');
					return;
				}
			}
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let {
				ware,
				value,
				kind
			} = this;
			let id, attrid;
			if (ware.attr.length == 0) {
				id = ware.id;
				attrid = 0;
			} else {
				id = ware.attr[kind].item_id;
				attrid = ware.attr[kind].id;
			}
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				item_id: id,
				attr_id: attrid,
				item_num: value
			};

			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			rs.postRequests('firstChangeNum', params, res => {
				let data = res.data;
				if (data.code == 200) {
					rs.Toast('加入购物车成功');
					// 分类
					let classify = uni.getStorageSync('classify');
					let newclassify = [];
					for (let i of classify) {
						if (i.id == id) {
							i.cart_num = data.data.new_num;
						}
						newclassify.push(i);
					}

					uni.setStorageSync('classify', newclassify);
					// 推荐
					let recommed = uni.getStorageSync('recommed');

					let newrecommed = [];
					for (let i of recommed) {
						if (i.id == id) {
							i.cart_num = data.data.new_num;
						}
						newrecommed.push(i);
					}
					uni.setStorageSync('recommed', newrecommed);
					// 收藏
					let collect = uni.getStorageSync('collect');
					let newcollect = [];
					for (let i of collect) {
						if (i.id == id) {
							i.cart_num = data.data.new_num;
						}
						newcollect.push(i);
					}
					uni.setStorageSync('collect', newcollect);
					// 查找
					let search = uni.getStorageSync('search');
					let newsearch = [];
					for (let i of search) {
						if (i.id == id) {
							i.cart_num = data.data.new_num;
						}
						newsearch.push(i);
					}
					uni.setStorageSync('search', newsearch);

					if (this.spec) {
						this.attrspec.cart_num = data.data.new_num;
					} else {
						this.ware.cart_num = data.data.new_num;
					}
					setTimeout(() => {
						if (cartUrl == true) {

							uni.switchTab({
								url: '/pages/tabar/shopcart'
							});
						}
					}, 500);
				} else if (data.code == 406 || data.code == 407) {
					rs.Toast('超出活动购买数量');
				} else {
					rs.Toast(res.data.msg);
				}
			});
		},
		toParent(e) {
			if (this.spec) {
				if (this.attrspec.is_float == 1 && !Number.isInteger(Number(e.val))) {
					rs.Toast('购买数量不能为小数');
					return;
				}
			}
			this.value = e.val;
			this.$refs.popup.close();
		}
	},
	onLoad(option) {
		//option为object类型，会序列化上个页面传递的参数
		this.id = option.id;
	},
	onShow() {
		this.getItem();
		getApp().globalData.isReload = false;
	},
	onPageScroll(e) {
		if (e.scrollTop == 0) {
			this.nav = false;
		} else {
			this.nav = true;
		}
	}
};
